.cnt-box {
  width: 80%;
  color: white;
  font-size: 0.9em;
  background: rgba(255, 255, 255, 0.3);
  padding: 24px 0;
  border-radius: 16px;
  margin: 0 auto;
  box-shadow: 0 0 30px rgba(255, 255, 255, 0.3);
  white-space: pre-wrap;
  position: relative;
}

.cnt-box .cnt {
  width: 100%;
  height: 100%;
  overflow: scroll;
  text-align: center;
  padding: 0 20px;
}