body {
  margin: 0;
  font-family: '萍方-简', 'Noto Serif SC', 'Source Han Sans SC', 'Ubuntu',
    'Helvetica Neue', 'Microsoft YaHei', Arial, serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

.mb {
  margin-bottom: 16px;
}

.mt {
  margin-top: 16px;
}

.mr {
  margin-right: 16px;
}

.ml {
  margin-left: 16px;
}

@media screen and (min-width: 768px) and (min-height: 1024px) {
  * {
    font-size: 1.25rem;
  }
  .mb {
    margin-bottom: 32px;
  }

  .mt {
    margin-top: 32px;
  }
  .mr {
    margin-right: 32px;
  }

  .ml {
    margin-left: 32px;
  }
}
/* iPad Pro */
@media screen and (min-width: 1024px) and (min-height: 1366px) {
}
