/* 全局容器 */
#root,
.full-screen {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.cntr {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.size-auto {
  flex: 1;
}
/* 背景容器*/
.bg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1000;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(0deg, #202032, #3f3e50);
  overflow: hidden;
}

.bg * {
  position: absolute;
}

.bg .cloud {
  width: 100vw;
  left: 0;
  bottom: 0;
}

.box {
  width: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.meteor {
  z-index: -999;
  width: 100px;
}

.bg .star {
  width: 100vw;
  top: 0;
  left: 0;
  z-index: -999;
}

.bg .moon {
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
  z-index: -999;
}

.img-btn {
  width: 56px;
  height: auto;
}

.box-btn {
  width: 80px;
  height: auto;
}

.btn-cntr {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px;
}

.input-password {
  color: white;
  font-size: 0.9em;
  background: rgba(255, 255, 255, 0.3);
  padding: 12px 16px;
  border-radius: 24px;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.3);
  border: none;
  width: 60%;
}

.input-password::placeholder {
  color: white;
}
.input-password:focus {
  outline: none;
}

.editor {
  width: 100%;
  height: 100%;
  background: transparent;
  outline: none;
  border: none;
  color: white;
  font-size: 1em;
  font-family: inherit;
  background: linear-gradient(transparent 92%, rgba(255, 255, 255, 0.5) 8%);
  line-height: 1.5rem;
  background-attachment: local;
  resize: none;
  background-size: 100% 1.5rem;
}
/* 分辨率适配 */
/* iPad */
@media screen and (min-width: 768px) and (min-height: 1024px) {
  .bg .cloud {
    bottom: -80px;
  }
  .bg .star {
    top: -120px;
  }
  .img-btn {
    width: 80px;
  }
  .btn-cntr {
    padding: 48px;
  }

  .input-password {
    padding: 24px 28px;
    border-radius: 48px;
  }
}
/* iPad Pro */
@media screen and (min-width: 1024px) and (min-height: 1366px) {
  .bg .cloud {
    bottom: -120px;
  }
}
